ms on the Website or in the Services, we will collect your name, email address and your comment or question that you enter into the form to answer your request, questions or concerns. When you post messages on the message boards of the Website or through the Services, the information contained in your posting will be stored on our servers and made public so other users will be able to see it. When you contact us by sending us an email, we will collect and store your email address and any information contained in the email you send us and any information attached to the email for so long as is necessary. This information is used to answer your email and address any questions and concerns you may have. We may also collect, from you, the following personal information about your contacts: Name and address of a gift recipient, in order to fulfill a purchase order When you provide us with personal information about your contacts we will only use this information for the specific reason for which it is provided. If you believe that one of your contacts has provided us with your personal information and you would like to request that it be removed from our database, please contact us at contact@WtxHub.com. ")])]),_c('div',{staticClass:"page-content-block"},[_c('h3',[_vm._v("PERSONAL INFORMATION COLLECTED VIA TRACKING TECHNOLOGY")]),_c('p',[_vm._v(" As you navigate our Website or App, certain information may also be passively collected, including your IP address, browser type, domain names, access times, operating system, mouse clicks, mouse movements, scrolling activity. We also use Cookies and navigational data like Uniform Resource Locators (URL) to gather information regarding the date and time of your visit and the solutions and information for which you searched and which you viewed. This type of information is collected to analyze the use of our Services and solutions and to make them more useful to you. Like other aspects of our Services, we use a third party servic